import Typography from 'components/Typography';
import Page from 'components/Page';
import React, {useState} from 'react';
import '../styles/pre.scss'

import {
  Card, CardBody, CardHeader, Col, Row
} from 'reactstrap';

function TemplatePage()
{

  const [templateId, setTemplateId] = useState('');

  const onChange = (e) => {
    setTemplateId(e.target.value);
  };

  return (
    <Page title="reactTemplate API">

      <Row>
        <Col>
          <Card>
            <CardHeader>설명</CardHeader>
            <CardBody>
              <Typography type="signup">
                NFT 발행을 손쉽게 도와주기 위한 Template API를 제공합니다. <br/>
              </Typography>
            </CardBody>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col>
          <Card>
            <CardHeader>Template 발행 API</CardHeader>
            <CardBody>
              <Typography>
                NFT Template 발행을 위한 API<br/>
                POST방식 : https://nft-api-dev.shinsegae-inc.com/template<br/>
              </Typography>
            </CardBody>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col>
          <Card>
            <CardHeader>Template 조회 API</CardHeader>
            <CardBody>
              <Typography> 
                NFT Template 조회를 위한 API<br/>
                GET방식 : https://nft-api-dev.shinsegae-inc.com/template= <p className='valuepre'>{templateId}</p><br/>
                
                tempalteId : <input onChange={onChange} value={templateId}/> 
                
              <pre className='precode'>
                async function getTemplateInfo(templateId)<br/>
                &#123;<br/>
                &nbsp;const options = <br/>
                &nbsp;&#123; <br/>
                &nbsp;&nbsp;&nbsp;hostname: 'nft-api-dev.shinsegae-inc.com', <br/>
                &nbsp;&nbsp;&nbsp;port: 443, <br/>
                &nbsp;&nbsp;&nbsp;path: '/template/info?template_id='+ <p className='valuepre'>{templateId}</p>, <br/>
                &nbsp;&nbsp;&nbsp;method: 'GET', <br/>
                &nbsp;&nbsp;&nbsp;headers: &#123; <br/>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'Content-Type': 'application/json', <br/>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'Authorization': authModel.getStoredToken(), <br/>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;, <br/>
                &nbsp;&#125; <br/>
                &nbsp;return await commonModel.httpRequest(options, "") <br/>
                &#125;
              </pre>



              </Typography>
            </CardBody>
          </Card>
        </Col>
      </Row>    

    </Page>
  );
};

export default TemplatePage;
